@import url(/node_modules/normalize.css/normalize.css);
.header {
  color: #ffffff;
  font-family: 'Proxima Nova Regular',sans-serif; }

.header__ad {
  margin: 20px auto 16px auto; }

.ad {
  display: flex;
  justify-content: center; }

.header-wrap {
  background: #2d3036; }

.header-wrap_color_light-grey {
  background: #3c3c43; }

.navigation-menu {
  font-size: 16px;
  display: flex;
  justify-content: space-between; }

.navigation-menu__empty-block {
  display: inline-block;
  width: 230px; }

.menu-list {
  padding: 0;
  margin: 0;
  width: 630px;
  height: 50px; }

.menu-list_size-m {
  font-family: 'Proxima Nova Bold';
  font-size: 10px;
  color: #28be64;
  text-transform: uppercase;
  height: 30px; }

.menu-list_footer {
  width: 420px; }

.navigation-menu__menu-list {
  display: flex;
  justify-content: space-between;
  align-content: center; }

.menu-list__item {
  display: flex;
  justify-content: center;
  flex-direction: column; }

.search {
  width: 230px;
  display: flex;
  align-items: center;
  justify-content: flex-end; }

.search__button {
  background: url(../../build/img/ico-search.jpg);
  height: 18px;
  width: 18px;
  border: none;
  margin-right: 5px; }

.search__text {
  background: none;
  border: none;
  width: 80px; }

.search__text::placeholder {
  color: #ffffff; }

.header-navigation-divider {
  border-top: 1px solid rgba(0, 0, 0, 0.2); }

.header-navigation-divider_color-darkest {
  border-color: rgba(0, 0, 0, 0.9); }

.header-navigation-divider_color_lightest {
  border-color: rgba(255, 255, 255, 0.2); }

.navigation-menu-all-themes {
  width: 230px;
  font-size: 10px;
  text-transform: uppercase;
  font-weight: bold;
  display: flex;
  align-items: center; }

.navigation-menu-all-themes__link {
  display: flex;
  align-items: center; }

.navigation-menu-all-themes__ico {
  margin-right: 5px; }

.container {
  width: 1180px;
  margin: 0 auto;
  padding: 0 40px; }

.container__header-logo {
  padding: 30px 0 15px 0;
  display: inline-block; }

.header-wrap__container {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.container__header-subscribe {
  display: inline-block; }

.header-subscribe__ico {
  margin-right: 5px; }

.header-subscribe__divider {
  margin: 0 10px;
  color: #1d1d1d; }

.row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  align-content: space-between; }

.article-list_first-list_margin {
  margin-top: 54px; }

.article-item {
  background: #fff;
  width: 720px;
  min-height: 460px;
  max-height: 630px; }

.article-item_size_s {
  width: 340px; }

.article-item_size_m {
  width: 530px; }

.article-item_margin_40 {
  margin-bottom: 40px; }

.article-item_margin_188 {
  margin-bottom: 188px; }

.article-item__img {
  height: 407px;
  width: 720px;
  background-color: #e3e3e3;
  display: flex;
  align-items: flex-end;
  background-size: cover; }

.article-item__img_size_s {
  width: 340px;
  height: 210px; }

.article-item__img_size_m {
  width: 340px;
  height: 320px; }

.article-item__img_ad {
  height: 500px;
  width: 300px;
  margin: 56px auto;
  background-image: url(../../build/img/article-ad.jpg); }

.article-item__img_article_1 {
  background-image: url(../../build/img/article-1.jpg); }

.article-item__img_article_2 {
  background-image: url(../../build/img/article-2.jpg); }

.article-item__img_article_3 {
  background-image: url(../../build/img/article-3.jpg); }

.article-item__img_article_5 {
  background-image: url(../../build/img/article-5.jpg); }

.article-item__img_article_8 {
  background-image: url(../../build/img/article-8.jpg); }

.article-item__img_article_9 {
  background-image: url(../../build/img/article-9.png); }

.article-item__img_article_10 {
  background-image: url(../../build/img/article-10.jpg); }

.article-item__img_article_11 {
  background-image: url(../../build/img/article-11.jpg); }

.article-item__img_article_12 {
  background-image: url(../../build/img/article-12.jpg); }

.article-item__img_article_13 {
  background-image: url(../../build/img/article-13.jpg); }

.article-item__img-replacement {
  height: 4px;
  background: #28be64; }

.article-item__tags {
  font-family: 'Proxima Nova Bold';
  font-size: 12px;
  text-transform: uppercase;
  color: #28be64; }

.article-item__tag-link {
  background: #fff;
  border-radius: 10px;
  padding: 2px 12px;
  margin-right: 10px; }

.article-item__tag-link:hover {
  background: #28be64;
  color: #ffffff; }

.article-item__tag-link_size_s {
  background: none;
  padding: 0; }

.article-item__tag-link_size_s:hover {
  background: none;
  color: #9474c1; }

.article-item__header {
  color: #ffffff;
  font-size: 36px;
  margin: 15px 0 0 0;
  font-family: 'Proxima Nova Bold'; }

.article-item__header_size_s {
  color: #1d1d1d;
  font-size: 18px;
  margin: 0 0 12px 0; }

.article-item__header-wrap {
  background: black;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.740415) 0%, rgba(255, 255, 255, 0) 100%); }

.article-item__header-wrap_inner {
  padding: 0 20px 30px 20px; }

.article-item__content {
  padding: 20px 20px 16px 20px;
  font-size: 15px;
  line-height: 22px; }

.article-item__content_size_s {
  padding-top: 8px;
  font-size: 13px; }

.article-item__divider {
  height: 4px;
  margin: 0 20px;
  background: #e3e3e3; }

.article-item__footer {
  display: flex;
  justify-content: space-between;
  font-family: Helvetica, sans-serif;
  font-size: 11px;
  font-weight: bold;
  padding: 12px 20px 18px; }

.article-item__author {
  display: inline-block; }

.article-item__date {
  display: inline-block;
  color: #A8A8A8;
  margin-left: 10px; }

.article-item__comments {
  display: flex; }

.article-item__comments-number {
  margin: 3px 0 0 5px; }

.page-header {
  margin: 53px 0 22px 0;
  font-family: 'Proxima Nova Bold';
  color: #28be64; }

.page-header::after {
  content: url(../../build/img/ico-arrow.jpg); }

.event {
  font-family: 'Proxima Nova Bold';
  background: #fff;
  width: 530px;
  height: 200px;
  padding: 10px; }

.event__img {
  width: 180px;
  height: 180px;
  background-color: #e3e3e3;
  background-size: cover;
  background-position: center;
  float: left; }

.event__img_pic_1 {
  background-image: url(../../build/img/event-1.jpg); }

.event__img_pic_2 {
  background-image: url(../../build/img/event-2.jpg); }

.event__content {
  margin-left: 190px; }

.event__tags {
  font-family: 'Proxima Nova Bold';
  font-size: 12px;
  text-transform: uppercase;
  color: #28be64; }

.event__tag-link:hover {
  background: none;
  color: #9474c1; }

.event__header {
  color: #1d1d1d;
  font-size: 18px; }

.event__date {
  font-family: 'Proxima Nova Regular';
  font-size: 15px;
  color: #28be64;
  background: #f9f9f9;
  width: auto;
  display: inline-block;
  padding: 5px;
  margin-bottom: 12px; }

.event__info {
  font-size: 10px;
  color: #A8A8A8; }

.footer {
  background-color: #3c3c43;
  height: 180px;
  padding-top: 20px; }

.footer-logo__img {
  float: left; }

.footer-copyright {
  font-size: 12px;
  font-family: 'Proxima Nova Regular', sans-serif;
  color: #A8A8A8; }

.container__footer-copyright {
  margin-left: 100px; }

.container__footer-copyright {
  margin-left: 100px; }

.container__menu-list_footer {
  margin: 80px 0 0 100px; }

.navigation-menu__link:hover {
  color: #9474c1; }

.navigation-menu__link_disabled {
  color: #ffffff; }

